import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
import baseURL from '../../config/index.js'
export default {
	onLoad() {
		this.initColectInfo()
		// this.initStart()
	},
	components:{
		uniLoadMore
	},
	data () {
		return {
			scrollList: ['我收藏的','我屏蔽的'],
			currentIndex: 0,
			collectInfo: [],
			hasFlag: false
		}
	},
	methods:{
		//切换导航
		handleScroll (index){
			this.currentIndex = index
		},
		async initColectInfo(){
			
			let res = await this.$fetch(this.$api.orderorderCollectionList,{type:this.currentIndex},"POST","Form")
			console.log(res)
			res.rows.forEach(item => {
				item.riderScore = Math.floor(item.riderScore / 20)
				
				if (item.riderAvatar) {
					var newsStr= item.riderAvatar.indexOf("http");
						//不是以http开头
					　　if(newsStr==-1){
							item.riderAvatar = baseURL + item.riderAvatar.slice(1,item.riderAvatar.length)
					　　}
				}
				
			})
			
			this.collectInfo = res.rows

			// Vue.set(this.collectInfo,res.rows)
		},
		//拨打电话
		callPhone (phone, id) {
			if (this.currentIndex == 0) {
				uni.showModal({
				    title: '拨打电话',
				    content: '是否拨打该骑手电话',
				    success: function (res) {
				        if (res.confirm) {
				           uni.makePhoneCall({
				               phoneNumber: phone //仅为示例
				           });
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});		
			} else {
				uni.showModal({
				    title: '取消屏蔽',
				    content: '是否取消屏蔽该骑手',
				    success: async (res) => {
				        if (res.confirm) {
							let msg = await this.$fetch(this.$api.orderCancelCollection, {riderId: id }, "POST", 'Form')
							console.log(msg)
							
							if (msg.code == 0) {
								uni.showToast({
									icon: 'none',
									title: '操作成功'
								})
								this.initColectInfo()
							} else {
								uni.showToast({
									icon: 'none',
									title: '操作失败'
								})
							}
							
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		}
		
	},
	watch:{
		currentIndex(){
			this.initColectInfo()
		}
	},
	
}
